import s from "./DropDownMenu.module.css";

export const DropDownMenu = ({
  menuItems,
  dropdownFlag,
  setDropdownFlag,
  setCurrentCategory,
}) => {
  const onCategoryClick = (e) => {
    if (e.target.innerHTML === " ") {
      setCurrentCategory(" ");
    }
    setCurrentCategory(e.target.innerHTML);
    setDropdownFlag(!dropdownFlag);
  };
  return (
    <ul className={s.menuList}>
      <li className={s.menuItem} onClick={onCategoryClick} key={" "}>
        <button className={s.btnCategory}> </button>
      </li>
      {menuItems.map((menuItem) => {
        if (
          menuItem !== "" &&
          menuItem !== "" &&
          menuItem !== ""
        ) {
          return (
            <li className={s.menuItem} onClick={onCategoryClick} key={menuItem}>
              <button className={s.btnCategory}>{menuItem}</button>
            </li>
          );
        }
        return null;
      })}
    </ul>
  );
};
