import { useState, useEffect } from "react";
import Backdrop from "Components/Backdrop/Backdrop";
import TelegramSendMessage from "api/TelegramSendMessage";
import randomID from "@mrenke/randomid-generator";
import s from "../ModalForm/ModalForm.module.css";
import "primeicons/primeicons.css";
import { InputNumber } from "primereact/inputnumber";

const INITIAL_USER_DATA = {
  userName: "",
  userTel: "",
  userAddress: "",
  userComment: "",
};

const ModalForm = ({ handleBackModalClick, setModalFlag, cart, setCart }) => {
  const [userData, setUserData] = useState(INITIAL_USER_DATA);
  const { userName, userTel, userAddress, userComment } = userData;

  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  let totalOrderedPrice = cart.reduce((acc, i) => {
    if (isNaN(i.Price * i.Order)) return acc;
    return acc + i.Price * i.Order;
  }, 0);
  const resetForm = () => {
    setUserData(INITIAL_USER_DATA);
  };
  const clearCart = () => {
    setCart([
      {
        Art: "",
        GoodName: " ",
        Amount: "  ",
        Price: "  .",
        Order: "",
      },
    ]);
  };
  const submitForm = (e) => {
    e.preventDefault();
    const userOrder = cart.slice(1);
    let messageToTG = `     \n
    : ${new Date().toLocaleString()} \n 
    : ${userName} \n 
    : ${userAddress}\n 
    : ${userTel} \n 
    : ${userComment} \n 
    : \n
    ${userOrder.map((i, ind) => {
      return `${ind + 1}  \n
      : ${i.Art} \n
      : ${i.GoodName} \n
        : ${i.Amount} \n
        : ${i.Price} \n
       : ${i.Order}  \n \n`;
    })}
    
      ${userOrder.length} . \n
     : ${userOrder
      .reduce((acc, i) => acc + i.Price * i.Order, 0)
      .toFixed(2)} . \n`;

    TelegramSendMessage(messageToTG);
    resetForm();
    setModalFlag(false);
  };
  const handleInputsChange = (e) => {
    const { name, value } = e.target;
    const newData = { ...userData };
    newData[name] = value;
    setUserData(newData);
  };
  return (
    <div className={s.modalAndBackWrap}>
      <Backdrop onClick={handleBackModalClick} />
      <div className={s.modal}>
        <i
          className={`pi pi-times-circle ${s.closeButton}`}
          onClick={handleBackModalClick}
        ></i>

        <p className={s.orderTitle}> </p>
        <ul className={s.ModalUL}>
          <li className={s.ModalItem} key={randomID()}>
            <p className={s.ArtNumber}></p>
            <p className={s.GoodName}> </p>
            <p className={s.Amount}>  </p>
            <p className={s.Price}>  </p>
            <div className={s.quantityBlock}>  </div>
            <div className={s.totalPrice}> </div>
          </li>
          {cart.map((i) => {
            if (isNaN(i.Order * i.Price)) return null;

            return (
              <li className={s.ModalItem} key={randomID()}>
                <p className={s.ArtNumber}>{i.Art}</p>
                <p className={s.GoodName}>{i.GoodName}</p>
                <p className={s.Amount}>{i.Amount}</p>
                <p className={s.Price}>{i.Price}</p>
                <InputNumber
                  inputId={i.Art}
                  value={i.Order}
                  onValueChange={(e) => {
                    i.Order = e.value;
                  }}
                  mode="decimal"
                  showButtons
                  buttonLayout="vertical"
                  style={{ width: "34px" }}
                  decrementButtonClassName="p-button-secondary"
                  incrementButtonClassName="p-button-secondary"
                  incrementButtonIcon="pi pi-plus"
                  decrementButtonIcon="pi pi-minus"
                  min={0}
                  max={1000}
                />
                <div className={s.totalPrice}>
                  {(i.Price * i.Order).toFixed(2)}
                </div>
              </li>
            );
          })}
          <p>
             {"  "}
            <b className={s.totalOrderedNumber}>
              {cart.length - 1}
            </b> . <br />
             :{" "}
            <b className={s.totalOrderedNumber}>
              {totalOrderedPrice.toFixed(2)}
            </b>{" "}
            .
          </p>
        </ul>
        <form action="submit" className={s.formWrap}>
          <div className={s.inputsWrap}>
            <label className={s.inputs}>
               '   :
              <input
                type="text"
                value={userName}
                onChange={handleInputsChange}
                name="userName"
                placeholder=" "
                required={true}
              />
            </label>
            <label className={s.inputs}>
                  :
              <input
                type="text"
                value={userAddress}
                onChange={handleInputsChange}
                name="userAddress"
                placeholder=". , .  22,  "
                required={true}
              />
            </label>
            <label className={s.inputs}>
                 :
              <input
                type="text"
                value={userTel}
                onChange={handleInputsChange}
                name="userTel"
                placeholder="+380501112233"
                required={true}
              />
            </label>
            <label className={s.commentWrap}>
               :
              <textarea
                className={s.commentInput}
                type="text"
                rows="8"
                cols="33"
                value={userComment}
                onChange={handleInputsChange}
                name="userComment"
                placeholder=" ,   "
              />
            </label>
          </div>

          <button className={s.buttonSubmit} type="submit" onClick={submitForm}>
             
          </button>
          <button className={s.buttonClear} type="reset" onClick={clearCart}>
             
          </button>
        </form>
      </div>
    </div>
  );
};

export default ModalForm;
